import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_REMOVE_COL_HEADER = Symbol('TABLE_REMOVE_COL_HEADER');

export function removeTableColHeader(node: Table) {
  return {
    type: TABLE_REMOVE_COL_HEADER,
    payload: {
      node,
    },
  };
}


interface TableRemoveTableColHeaderPayload {
  node: Table;
}

interface TableRemoveTableColHeaderAction {
  type: typeof TABLE_REMOVE_COL_HEADER;
  payload: TableRemoveTableColHeaderPayload;
}

export function isTableRemoveTableColHeaderAction(
  action: Action,
): action is TableRemoveTableColHeaderAction {
  return action.type === TABLE_REMOVE_COL_HEADER;
}
